.box {
  position: relative;
  margin: 70px auto 8px;
  width: 220px;
  background-color: #fffcf6;
  border: 1px solid #ffe5a3;
  box-shadow: 0 0 8px rgba(175, 125, 31, 0.25);
  border-radius: 10px;
  padding: 10px;
}

.productImg {
  position: absolute;
  width: 100px;
  height: 100px;
  top: -50px;
  left: 0;
  right: 0;
  margin: 0 auto;
  text-align: center;

  img {
    max-width: 100px;
    max-height: 100px;
  }
}

.title {
  position: relative;
  margin-top: 50px;
  text-align: center;
  font-size: 20px;
  color: #8c5120;

  &:after {
    content: "";
    position: absolute;
    margin: 0 auto;
    bottom: -10px;
    left: 0;
    right: 0;
    width: 60%;
    height: 1px;
    background-color: #c7b076;
  }
}

.priceBlock {
  display: flex;
  align-items: center;
  margin-top: 20px;
  padding: 0 5px;
}

.priceIcon {
  svg {
    width: 30px;
    height: 30px;
  }
}

.priceText {
  margin-left: 3px;
  font-size: 20px;
  color: #8c5120;
}

.oldPriceBlock {
  position: relative;
  width: 1px;
  height: 100%;
}

.oldPriceText {
  position: absolute;
  bottom: 0;
  left: 5px;
  color: #98673e;
  opacity: 0.5;
  text-decoration: line-through;
}

.cartIcon {
  flex-grow: 1;
  text-align: right;

  svg {
    width: 47px;
    height: 30px;
    cursor: pointer;
  }
}
