.box {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}

.title {
  font-size: 22px;
  color: #8c5120;
}

.moreButton {
  font-size: 16px;
  background-color: #fffcf6;
  color: #a58022;
  padding: 2px 7px;
  border: 1px solid #fceccd;
  border-radius: 5px;
}

.line {
  flex: 1 0 auto;
  margin: 0 15px;
  height: 5px;
  border-bottom: 1px dashed #c7b076;
}
